<template>
  <div class="user-wrap">
    <div class="user-header">
      <van-icon 
        class="user-avatar fl"
        name="user-o" 
        @click="goLogin"
        size="50"
      />
      <div class="info fr">
        <p class="name">{{nickName}}</p>
        <p class="tel">{{phone}}</p>
        <p class="money">钱包余额：¥ {{money}}</p>
      </div>
      <van-icon 
        class="setting"
        name="setting-o" 
        @click="goSetting"
        size="24"
      />
    </div>
    <van-row class="user-links">
      <van-col span="2"></van-col>
      <van-col span="4">
        <van-icon name="pending-payment" />
        待审核
      </van-col>
      <van-col span="4">
        <van-icon name="records" />
        交易中
      </van-col>
      <van-col span="4">
        <van-icon name="tosend" />
        纠纷中
      </van-col>
      <van-col span="4">
        <van-icon name="logistics" />
        已关闭
      </van-col>
      <van-col span="4">
        <van-icon name="logistics" />
        已完成
      </van-col>
      <van-col span="2"></van-col>
    </van-row>
    <van-cell-group>
      <van-cell icon="balance-o" title="我的钱包" is-link />
      <van-cell icon="records" title="全部订单" is-link />
      <van-cell icon="service-o" title="我的客服" is-link />
      <van-cell icon="orders-o" title="公告" is-link />
      <van-cell icon="smile-comment-o" title="交流群" is-link />
    </van-cell-group>
    <TabBar />
  </div>
</template>

<script>
import TabBar from '@/components/TabBar'
import avatar_default from '@/assets/images/avatar_default.png'

export default {
  name: 'index',
  components: {
    TabBar
  },
  data () {
    return {
      nickName: '百杰票务',
      phone: '13010001000',
      money: 353,
      avatar: avatar_default
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    goLogin () {
      this.$dialog.confirm({
        message: '是否退出登录'
      }).then(() => {
        this.$router.push({ path: '/login' })
      }).catch(() => {
        // on cancel
      })
    },
    goSetting () {
      this.$router.push({ path: '/setting' })
    }
  }
}
</script>



